a {
    text-decoration: none;
    color: black;
    font-size: 12px;
}

.nav {
     /* grid-row: 2/3; */
    margin-top: 10px;
    padding-top: 10px;
    width: 30px;
    height: 230px;
    border-radius: 30px;
    background-color: #eceaea;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
}

.item {
    text-align: center;
    border-radius: 50%;
}

i {
    background: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.icons {
    transition: transform 0.3s;
    width: 15px;
    height: 15px;
    padding-bottom: 20px;
}


.icons:hover {
    transform: scale(1.4);
}

.item a:active,
.item a:focus {
    font-weight: 700;
    font-size: 16px;
}
